<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>教师管理</el-breadcrumb-item>
      <el-breadcrumb-item>教师列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="教师名称" prop="teaName">
              <el-input
                v-model="queryVo.teaName"
                @keyup.enter.native="queryPage"
                placeholder="请输入教师名称"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            
            <el-form-item label="课程名" prop="cname">
              <el-input
                v-model="queryVo.cname"
                @keyup.enter.native="queryPage"
                placeholder="请输入课程名"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>

            <el-form-item label="班级" prop="className">
              <el-input
                v-model="queryVo.className"
                @keyup.enter.native="queryPage"
                placeholder="请输入班级"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>

            <el-form-item label="学期" prop="tcTerm">
              <el-input
                v-model="queryVo.tcTerm"
                @keyup.enter.native="queryPage"
                placeholder="请输入学期"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          type="index"
          width="80"
          align="center"
          label="用户编号"
        ></el-table-column>

        <el-table-column
          prop="teaName"
          align="center"
          label="教师名称"
        ></el-table-column>

        <el-table-column
          prop="cname"
          label="课程名称"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="className"
          label="班级名称"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="tcTerm"
          label="授课学期"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="tcDemo"
          label="备注"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="教师" prop="teaId">
                <el-select
                  v-model="Form.teaId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in teaOptions"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
               <el-form-item label="授课学期" prop="tcTerm">
                <el-input v-model="Form.tcTerm"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="课程名称" prop="courseNumber">
                <el-select
                  v-model="Form.courseNumber"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in corOptions"
                    :key="item.id"
                    :label="item.cname"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="班级名称" prop="classId">
                <el-select
                  v-model="Form.classId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in classOptions"
                    :key="item.id"
                    :label="item.className"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="备注" prop="tcDemo">
            <el-input v-model="Form.tcDemo"></el-input>
          </el-form-item>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
        corOptions:[],
        classOptions:[],
      teaOptions: [],
      Form: {},
      ids: [],
      open: false,
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        teaName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      rules: {
        classId: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        courseNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        teaId: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        cname: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        className: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        tcTerm: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        wdepart: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        tcDemo: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        teaDepartId: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wduties: [{ required: true, message: '该项不能为空', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/teaBaseInfo/queryTeaBaseInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: clres } = await this.$http.post(
        '/classInfo/queryClassInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: cores } = await this.$http.post(
        '/couresInfo/queryCouresInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      this.teaOptions = res.data.dataList.records
      this.classOptions = clres.data.dataList.records
      this.corOptions = cores.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(this.classOptions)
      console.log(this.corOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/teaCourseInfo/saveOrUpdateTeaCourseInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/teaCourseInfo/getByIdTeaCourseInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
      this.Form.classId = this.Form.classId+''
    },

    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      //   console.log(row);
      const userid = a || this.ids
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/teaCourseInfo/removeByIdTeaCourseInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/teaCourseInfo/queryTeaCourseInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    },
    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
